.DetailContracted {
  position: fixed;
  top: 0;
  left: 581px;
  height: 100%;
  min-width: 500px;
  width: calc(100% - 581px);
}
.ShortcutContracted {
  position: fixed;
  top: 0;
  left: 240px;
  height: 100%;
  min-width: 500px;
  width: calc(100% - 240px);
}
.ShortcutContractedCollapse {
  position: fixed;
  top: 0;
  left: 55px;
  height: 100%;
  min-width: 500px;
  width: calc(100% - 55px);
}
.DetailExpanded {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  transition: all 0.3s;
  transform: translateX(0%);
  height: 100%;
  min-width: 500px;
}
.DetailContractedCollapse {
  position: fixed;
  top: 0;
  left: 396px;
  height: 100%;
  width: calc(100% - 396px);
  min-width: 500px;
}
.DetailContentContractedCollapse {
  overflow-y: auto;
  height: calc(100% - 105px);
  width: 100%;
}
.DetailHeader {
  z-index: -1;
  width: 100%;
  height: 105px;
  border-bottom: 1px solid #eee;
}
.ShortcutContentContracted {
  overflow-y: auto;
  height: calc(100% - 105px);
  width: 100%;
}
.DetailContentContracted {
  overflow-y: auto;
  height: calc(100% - 105px);
  width: 100%;
}
.DetailContentExpanded {
  width: 100%;
  overflow-y: auto;
  overflow-x: hidden;
  height: calc(100% - 105px);
}
.InputFields {
  margin: 55px 0px 0px 40px;
}
.ExpandBtn {
  height: 25px;
  width: 25px;
  margin: 12px;
  border: 0;
  border-radius: 2px;
  display: inline-flex;
  background-color: #eee;
  cursor: pointer;
  transition: 0.3s ease-out;
  align-items: center;
}
.ExpandBtn:hover {
  background-color: rgb(173, 172, 180, 0.4);
}
.NotebookBtn {
  border: 0;
  padding: 5px;
  border-radius: 3px;
  background-color: inherit;
  display: block;
  color: #555;
  cursor: pointer;
  transition: 0.3s ease-out;
  max-width: 200px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.NotebookIcon {
  position: relative;
  top: 1px;
  left: 0;
}
.NotebookBtnTitle {
  font-weight: 300;
  margin-left: 5px;
}
.NotebookBtn:hover {
  background-color: rgb(173, 172, 180, 0.4);
}
.HeaderTopBtns {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.HeaderTopLeftBtns {
  display: inline-flex;
  align-items: center;
}
.HeaderBottomBtns {
  margin-top: 25px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.OptionsBtn {
  padding-top: 3px;
  height: 30px;
  width: 25px;
  display: flex;
  border-radius: 2px;
  justify-content: center;
  border: 0;
  background-color: inherit;
  color: #555;
  cursor: pointer;
  transition: 0.3s ease-out;
  margin-right: 15px;
}
.OptionsBtn:hover {
  background-color: rgb(173, 172, 180, 0.4);
}
.LastUpdated {
  cursor: default;
  font-size: 13px;
  color: rgb(173, 172, 180);
  font-weight: 330;
  margin-left: 20px;
}
.Saved {
  cursor: default;
  font-size: 13px;
  color: rgb(173, 172, 180);
  font-weight: 330;
  margin-right: 25px;
}
.SavedSpan {
  display: flex;
  align-items: center;
}
.SavedSpan span {
  margin-bottom: 1px;
  margin-left: 4px;
}
@media (max-width: 600px) {
  .DetailContracted {
    left: 400px;
    width: calc(100% - 400px);
  }
  .ShortcutContracted {
    left: 200px;
    width: calc(100% - 200px);
  }
  .DetailContractedCollapse {
    left: 256px;
    width: calc(100% - 256px);
  }
}
